import GameState from 'states/GameState';
import Scene2 from 'states/Scene2';
import Scene3 from 'states/Scene3';
import Scene4 from 'states/Scene4';

class Game extends Phaser.Game {

	constructor() {
		
		
		if(window.innerWidth>window.innerHeight){
			window.landscape = true;
			window.w = window.innerWidth;
			window.h = window.innerHeight;
			let ratio = window.innerHeight/768;
			super(window.innerWidth/ratio, 768, Phaser.WEBGL, 'content');
		}else{
			window.landscape = false;
			window.w = window.innerHeight;
			window.h = window.innerWidth;
			let ratio = window.innerWidth/768;
			super(768, window.innerHeight/ratio, Phaser.WEBGL, 'content');
		}
		
		this.preserveDrawingBuffer = true;
		this.state.add('GameState', GameState,false);
		this.state.add('Scene2', Scene2, false);
		this.state.add('Scene3', Scene3, false);
		this.state.add('Scene4', Scene4, false);
		this.state.start('GameState');
	  
	//	this.scale.scaleMode = Phaser.ScaleManager.SHOW_ALL; 
		
	}

}




window.game = new Game();

var attachFastClick = Origami.fastclick;
attachFastClick(document.body);